<template>
    <view>
        <!-- 录音 -->
        <view class="voice-box">
            <view style="height: 90rpx;"></view>
            <view v-if="!isPlayShow">
                <!-- 开启的圆圈 -->
                <view @click="toRecord()" class="voice-box-circle">
                    <view class="voice-box-circle-item-box" :style="{
                        'background-color': isRecording ? 'rgba(118,118,118,.4)' : '#666666'
                    }">
                        <view v-if="isRecording">
                            <view class="voice-box-circle-item" :style="bigIndex == 0 ? 'transform: scale(1.5);' : ''">
                            </view>
                            <view class="voice-box-circle-item" :style="bigIndex == 1 ? 'transform: scale(1.5);' : ''">
                            </view>
                            <view class="voice-box-circle-item" :style="bigIndex == 2 ? 'transform: scale(1.5);' : ''">
                            </view>
                        </view>
                    </view>
                </view>

                <!-- 时间 -->
                <view class="voice-box-time">
                    <text>{{ toSecond(time)}}</text>
                </view>
                <view style="height: 40rpx;"></view>
            </view>



            <view v-if="isPlayShow" style="position: relative;">
                <view @click="toDelete()" class="voice-img-box" style="background-color: rgba(0,0,0,.2);left: 100rpx;">
                    <image :src="$kt.file.byPath('image/icon/delete.svg')" class="voice-img-box-img"></image>
                </view>

                <!-- 开启的圆圈 -->
                <view @click="toPlay()" class="voice-box-circle">
                    <view class="voice-box-circle-item-box">
                        <view style="margin-top:25rpx">
                            <view class="voice-box-circle-item2" :style="bigIndex == 0 ? 'transform: scale(1.2);' : ''">
                            </view>
                            <view class="voice-box-circle-item2"
                                :style="bigIndex == 1 ? 'transform: scale(1.2);' : 'transform: scale(.8);'">
                            </view>
                            <view class="voice-box-circle-item2" :style="bigIndex == 2 ? 'transform: scale(1.2);' : ''">
                            </view>
                        </view>
                    </view>
                </view>

                <view class="voice-img-box" @click="toSubmit()" style="background-color: #333;right: 100rpx;">
                    <image :src="$kt.file.byPath('image/icon/tick.svg')" class="voice-img-box-img"></image>
                </view>

                <!-- 时间 -->
                <view class="voice-box-time">
                    <text>{{ toSecond(time)}}</text>
                </view>
                <view style="height: 40rpx;"></view>
            </view>



        </view>

    </view>
</template>

<script>

const recorderManager = uni.getRecorderManager();
const innerAudioContext = uni.createInnerAudioContext();

innerAudioContext.autoplay = true;

export default {
    data() {
        return {
            open: true,
            isRecording: false,
            isPlayShow: false,
            bigIndex: -1,
            timer: null,
            time: 0,
            voicePath: null,
            isPlaying: false,
            // 录音开始时间
            startTime: 0,
            isStoping: false,
        }
    },
    mounted() {
        recorderManager.onStop((res) => {
            console.log('recorder stop' + JSON.stringify(res));
            this.time = res.duration;
            this.voicePath = res.tempFilePath;
        });
        recorderManager.onPlay(res=>{
                    console.log(res)
        })
    },
    methods: {
        /**
         * 将毫秒转换成秒
         */
        toSecond(time) {
            return Math.floor(time / 1000) + "s";
        },

        toSubmit() {
            setTimeout(() => {

                this.$emit('submit', {
                    type: 'voice',
                    path: this.voicePath,
                    duration: this.time,
                });
            }, 100);
        },
        toRecord() {
            if (!this.isRecording) {
                this.startRecord();
            } else {
                this.endRecord();
            }
        },
        // 开始录音
        startRecord() {
            if (this.isRecording) {
                this.endRecord();
                return;
            }
            this.isRecording = true;
            this.time = 0;
            this.bigIndex = 0;
            recorderManager.start({
                duration: 65000,
            });
            this.startTime = new Date().getTime();

            var timer = setInterval(() => {
                if (this.time >= 60000) {
                    this.endRecord();
                }
            }, 10);

            this.timer = setInterval(() => {
                this.bigIndex++;
                // 获取录音时长
                this.time = new Date().getTime() - this.startTime;
                if (this.bigIndex > 2) {
                    this.bigIndex = 0;
                }
            }, 500);
        },
        // 结束录音
        endRecord() {
            if(this.isStoping){
                return false;
            }

            this.isStoping = true;
            setTimeout(() => {
                recorderManager.stop();
                this.isRecording = false;
                this.isPlayShow = true;
                clearInterval(this.timer);
                this.bigIndex = -1;
                this.isStoping = false;
                console.log(this.time)
                if(this.time<1000){
                    uni.showToast({
                        title: '录制时间太短了',
                        duration: 1000,
                        icon: 'none',
                    });
                    this.toDelete();
                }
            }, 500);
        },
        toPlay() {
            if (this.isPlaying) {
                this.stopRecord();
            } else {
                this.playRecord();
            }
        },
        // 播放录音
        playRecord() {
            if (this.isPlaying) {
                return false;
            }

            if (this.voicePath) {
                this.isPlaying = true;
                innerAudioContext.src = this.voicePath;
                innerAudioContext.play();
                var index = 0;
                this.timer = setInterval(() => {
                    index++;
                    this.bigIndex = index % 3;
                }, 500);

                var isReturn = false;
                var timerIn = setInterval(() => {
                    if (!this.isPlaying) {
                        isReturn = true;
                        clearInterval(timerIn);
                    }
                }, 10);

                setTimeout(() => {
                    if (isReturn) {
                        return false;
                    }
                    this.isPlaying = false;
                    clearInterval(this.timer);
                    this.bigIndex = -1;
                }, this.time);
            }

        },
        stopRecord() {
            innerAudioContext.stop();
            clearInterval(this.timer);
            this.isPlaying = false;
            this.bigIndex = -1;
        },
        toDelete() {
            innerAudioContext.stop();
            this.time = 0;
            clearInterval(this.timer);
            this.voicePath = null;
            this.isPlayShow = false;
            this.bigIndex = -1;
        },

    },


}
</script>


<style lang="scss" scoped>
.voice-box {
    width: 100%;
    height: 100%;
    text-align: center;

    .voice-box-circle {
        position: relative;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background-color: #f0f0f0;
        display: inline-block;
        text-align: center;
        font-size: 28rpx;

        .voice-box-circle-item {
            display: inline-block;
            width: 10px;
            height: 10px;
            border-radius: 50%;
            background-color: #333333;
            margin: 0 5px;
        }

        .voice-box-circle-item2 {
            display: inline-block;
            width: 10px;
            height: 30px;
            border-radius: 10rpx;
            background-color: #ffffff;
            margin: 0 5px;
        }

        .voice-box-circle-item-box {
            position: absolute;
            left: 50%;
            top: 50%;
            transform: translate(-50%, -50%);
            background-color: #666;
            width: 150rpx;
            height: 150rpx;
            border-radius: 55%;
            line-height: 150rpx;
        }
    }

    .voice-box-time {
        margin-top: 10px;
        font-size: 20px;
        font-weight: bold;
        color: #333;
    }
}

.voice-img-box {
    position: absolute;
    top: 60rpx;
    text-align: center;
    width: 100rpx;
    padding-top: 10rpx;
    border-radius: 50rpx;

    .voice-img-box-img {
        width: 50rpx;
        height: 50rpx;
    }
}

.voice-img-box:active {
    transform: scale(.8);
}
</style>